@import '../../styles/theme';

.blogs-page {

    .filter-view {
        padding: 15px 0PX;
        margin-bottom: 15px;
        background-color: var(--color-white);
    }

    table {
        background-color: var(--color-white);
        thead {
            border-bottom-width: 1px;
            th {
                border: 1px solid var(--color-grey);
                text-transform: uppercase;
                font-size: var(--font-size-12);
                font-weight: 500;
            }
        }
        tbody {
            tr:hover {
                background-color: var(--color-grey);

            }
            td {
                border: 1px solid var(--color-grey);
                font-size: var(--font-size-12);
                font-weight: 500;
            }
        }
    }

    .table-skeleton {
        background-color: var(--color-white);
        height: 43px;
        margin-bottom: 1px;
        display: flex;
        align-items: center;
        padding: 0 10px;
        justify-content: space-between;
        div {
            width: 100px;
            background-color: var(--color-grey);
            border-radius: 4px;
            height: 16px;
        }
    }
}
